import React from 'react';
import Latex from 'react-latex';
export default function Traco(){ return(<>
<h2>
    Traço
</h2>

O traço de uma matriz é a soma dos elementos da diagonal

<Latex displayMode={true}>{'$$'+
'tr(A)=\\sum^{n}_{i=1}a_{ii}'
+'$$'}</Latex>

<b>Propriedades :</b>
<ul>
    <li>
        <Latex>{"$tr(A+B)=tr(A)+tr(B)$"}</Latex>
    </li>
    <li>
        <Latex>{"$\\forall\\alpha\\in\\mathbb{R},tr(\\alpha A)=\\alpha tr(A)$"}</Latex>
    </li>
    <li>
        <Latex>{"$tr(ABC)=tr(BCA)=tr(CAB)$"}</Latex> <i>*A ordem das matrizes deve ser preservada</i>, <Latex>{"$tr(ABC)\\mathrel{\\char`≠}tr(BAC)$"}</Latex>
    </li>
</ul>


</>)}